@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
body{
  margin:0px;
}
.navbar {
    
    background:black;
    height: 90px;
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    font-family:"Ubuntu";
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: inline-flex;
    justify-content: center;
    height:120px;
  }

  .navbar-logo img{
      width: 7rem;
      height: 7rem;
      margin-left: 0.5rem;
      align-self: left;
    
  }
  
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 5px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content:center;
    margin-right: -5rem;
  }
  
  .nav-item {
    height: 20px;
    padding:0px;
    margin:1rem;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    height: 20px;
    font-size: 25px;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid rgb(187, 21, 21);
    border-radius: 0px 0px 25px 25px;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
    font-size: 5rem;
    margin-top:-1rem;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
    .navbar-logo img{
      width:5rem;
      height:5rem;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: linear-gradient(40deg, rgb(80,80,80) 0%, rgb(26, 23, 23) 100%);
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-menu.active .nav-links {
      text-align: center;
      font-size: 40px;
      margin-bottom:0px;
      width: 100%;
      display: table;
    }
  
    .nav-menu.active .nav-links:hover {
      color: rgb(129, 21, 21);
      border-radius: 0;
      border:0;
    }
  
    .navbar-logo {
      position: relative;
      top: -35px;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 5rem;
      margin-top:-1rem;
    }
  }
:root {
    --primary: #fff;
  }
  
  .btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
    margin-bottom:10px;
  }
  
  .btn--primary {
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
  }
  
  .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }
  @media screen and (max-width:960px){
      .btn--large {
    padding: 5px 1px;
    font-size: 10px;
  }
  }
  
  
  .btn--large {
    padding: 12px 26px;
    font-size: 20px;
  }
  
  .btn--large:hover,
  .btn--medium:hover {
    transition: all 0.3s ease-out;
    background: rgb(165, 19, 19);
    color: #ffffff;
    transition: 250ms;
  }
.PictureSectionContainer{
   position:relative; 
   height: 25rem;
   width:100%;
   display:flex;
   flex-direction: column;
   align-items: center;
   object-fit: contain;
   font-family: "Ubuntu";
}
.PictureSectionContainer video{
    object-fit: cover;
    height: 50%;
    width:100%;
    height: 40rem;
    position:absolute;
    z-index: -1;
}

.PictureSectionContainer h1{
    color:rgba(172, 67, 67);
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    font-size: 7rem;
    margin-bottom:0px;
}

.PictureSectionContainer h3{
    color:white;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    font-size:5rem;
    margin-top:0px;
}

@media screen and (max-width:960px){
    .PictureSectionContainer > h1{    
        color:rgba(172, 67, 67);
        width:-webkit-fit-content;
        width:-moz-fit-content;
        width:fit-content;
        font-size: 4rem;
        margin-bottom:0px;
    }
    .PictureSectionContainer h2{
        color:white;
        width:-webkit-fit-content;
        width:-moz-fit-content;
        width:fit-content;
        font-size:3rem;
        margin-top:0px;
        
    }
}
@media screen and (max-width:720px){
    .PictureSectionContainer h1{
        color:rgba(172, 67, 67);
        width:-webkit-fit-content;
        width:-moz-fit-content;
        width:fit-content;
        font-size: 3rem;
        margin-bottom:0px;
    }
    .PictureSectionContainer h2{
        color:white;
        width:-webkit-fit-content;
        width:-moz-fit-content;
        width:fit-content;
        font-size:2.5rem;
        margin-top:0px;
    }
}
.HeaderText{
    margin-top:20rem;
    text-align:center;
    font-size:4rem;
    color:white;
    font-family:"Ubuntu";
}
.HeaderText img{
    height: 50rem;
    width:30rem;
}
.HeaderTextContainer{
    display:inline-flex;
    align-items:center;
    max-width: 70rem;

}
.HeaderText h1 span {
    font-size:7rem;
    position: relative;
    padding-bottom: 5px;  
}
.HeaderText h1 span::after{
    position: absolute; 
    left: 0; 
    bottom: 0; 
   
    width: 100%; 
    height: 1px;
    content: "";
    border-bottom: 12px solid #DB0000;
    border-radius: 0px 0px 40px 40px;
}


.HeaderTextContainer h3{
    display:inline-block;
    color:white;
    font-size: 1.5rem;
    text-align: center;
    white-space: pre-line;
}

  
  @media only screen and (max-width: 1024px) {
    .HeaderText img {
      width:20rem;
      height:35rem;
    }
  }
    
  @media only screen and (max-width: 960px) {
    .HeaderText h1 span{
      font-size:5rem;
    }
  }
